import React, { useMemo, useState, useRef, useEffect, useCallback } from "react";
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";
import './MCOTables.css'; // Assuming your CSS file is here

const MCOImpactReportMBOMConnectionTable = ({ columns, data }) => {
  const [searchText, setSearchText] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  // --- Column Resizing State and Logic ---
  // Initialize column widths, could be default or from columns prop
  const [columnWidths, setColumnWidths] = useState(() => {
    const initialWidths = {};
    // FIX: Check if columns is an array before calling forEach
    if (Array.isArray(columns)) {
      columns.forEach(col => {
        // You might want a default width or pass it in via columns prop
        initialWidths[col.accessorKey] = col.defaultWidth || 150; // Default width if not specified
      });
    }
    return initialWidths;
  });

  const resizingColumnRef = useRef(null); // Stores the accessorKey of the column currently being resized
  const startXRef = useRef(0); // Stores the initial mouse X position
  const startWidthRef = useRef(0); // Stores the initial width of the column

  const handleMouseDown = useCallback((e, accessorKey) => {
    // Prevent text selection during drag
    e.preventDefault();
    resizingColumnRef.current = accessorKey;
    startXRef.current = e.clientX;
    startWidthRef.current = columnWidths[accessorKey];

    // Add global event listeners to handle drag and release anywhere on the document
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  }, [columnWidths]);

  const handleMouseMove = useCallback((e) => {
    if (resizingColumnRef.current === null) return;

    const currentX = e.clientX;
    const diff = currentX - startXRef.current;
    const newWidth = Math.max(50, startWidthRef.current + diff); // Minimum width of 50px

    setColumnWidths(prevWidths => ({
      ...prevWidths,
      [resizingColumnRef.current]: newWidth
    }));
  }, []); // Dependencies are not needed as refs are used

  const handleMouseUp = useCallback(() => {
    resizingColumnRef.current = null;
    startXRef.current = 0;
    startWidthRef.current = 0;

    // Remove global event listeners
    document.removeEventListener('mousemove', handleMouseMove);
    document.removeEventListener('mouseup', handleMouseUp);
  }, [handleMouseMove]); // Dependency for cleanup

  // --- End Column Resizing State and Logic ---

  // Filtered data
  const filteredData = useMemo(() => {
    // Ensure data is an array before filtering
    if (!Array.isArray(data)) {
      return [];
    }
    return data.filter((row) =>
      // Ensure columns is an array before iterating with .some
      Array.isArray(columns) && columns.some((col) =>
        String(row[col.accessorKey] || "") // Use empty string if row[col.accessorKey] is undefined
          .toLowerCase()
          .includes(searchText.toLowerCase())
      )
    );
  }, [searchText, data, columns]);

  // Paginated data
  const paginatedData = useMemo(() => {
    const startIndex = (currentPage - 1) * itemsPerPage;
    // Ensure filteredData is an array before slicing
    if (!Array.isArray(filteredData)) {
      return [];
    }
    return filteredData.slice(startIndex, startIndex + itemsPerPage);
  }, [filteredData, currentPage]);

  const totalPages = Math.ceil(filteredData.length / itemsPerPage);

  // Added handleExport function, assuming you might need it for this table too
  const handleExport = () => {
    // Ensure filteredData is an array before exporting
    if (!Array.isArray(filteredData) || filteredData.length === 0) {
      console.warn("No data to export.");
      return;
    }
    const worksheet = XLSX.utils.json_to_sheet(filteredData);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");

    const excelBuffer = XLSX.write(workbook, {
      bookType: "xlsx",
      type: "array",
    });

    const blob = new Blob([excelBuffer], {
      type: "application/octet-stream",
    });
    saveAs(blob, "exported-data.xlsx");
  };

  return (
    <div className="container-fluid p-0 mt-3">
      <div className="d-flex justify-content-between align-items-center mb-3">
        {/* Search Input and Export Button can go here */}
        <input
          type="text"
          className="form-control w-25"
          placeholder="Search..."
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        <button className="btn btn-success" onClick={handleExport}>
          Export to Excel
        </button>
      </div>

      <div className="table-responsive">
        <table className="table custom-table table-hover">
          <thead className="thead-light">
            <tr>
              {/* Add a check for columns being an array before mapping */}
              {Array.isArray(columns) && columns.map((col) => (
                <th
                  key={col.accessorKey}
                  style={{ width: columnWidths[col.accessorKey] + 'px' }}
                >
                  {col.header}
                  <div
                    className="resizer"
                    onMouseDown={(e) => handleMouseDown(e, col.accessorKey)}
                  />
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {/* Add a check for paginatedData being an array before checking length and mapping */}
            {Array.isArray(paginatedData) && paginatedData.length > 0 ? (
              paginatedData.map((row, idx) => (
                <tr key={idx}>
                  {/* Add a check for columns being an array before mapping */}
                  {Array.isArray(columns) && columns.map((col) => (
                    <td
                      key={col.accessorKey}
                      style={{ width: columnWidths[col.accessorKey] + 'px' }}
                    >
                      {row[col.accessorKey]}
                    </td>
                  ))}
                </tr>
              ))
            ) : (
              <tr>
                {/* Ensure columns is an array before trying to get its length */}
                <td colSpan={Array.isArray(columns) ? columns.length : 1} className="text-center">
                  No data found.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      <div className="d-flex justify-content-between align-items-center mt-3">
        <p className="mb-0">
          Showing {(currentPage - 1) * itemsPerPage + 1} to{" "}
          {Math.min(currentPage * itemsPerPage, filteredData.length)} of{" "}
          {filteredData.length} entries
        </p>

        <div>
          <button
            className="btn btn-outline-primary mx-1"
            disabled={currentPage === 1}
            onClick={() => setCurrentPage((prev) => prev - 1)}
          >
            Prev
          </button>
          <span>{currentPage}</span>
          <button
            className="btn btn-outline-primary mx-1"
            disabled={currentPage === totalPages}
            onClick={() => setCurrentPage((prev) => prev + 1)}
          >
            Next
          </button>
        </div>
      </div>
    </div>
  );
};

export default MCOImpactReportMBOMConnectionTable;